@media screen and (max-width: 1450px) {
  .keys {
    padding: 4px 0 0 20px;
  }

  .keys>div {
    display: inline-block;
    padding-right: 0;
    width: 44%;
    text-align: left;
  }



}


@media screen and (max-width: 1000px) {

  .leftPanel,
  .rightPanel {
    overflow: auto;
  }

  body {
    font-size: 20px;
    line-height: 26px;
  }

}



@media screen and (max-width: 850px) {

  .leftPanel,
  .rightPanel {
    overflow: auto;
  }

  #mainMenu li {
    padding: 0 20px;
  }
}

@media screen and (max-width: 700px) {
  .rightPanel {
    display: none;
  }

  .leftPanel {
    border-right: none;
    float: none;
    width: 100%;
    height: 100%;
  }

}

@media screen and (max-width: 600px) {
  #mainMenu li {
    padding: 0 8px;
  }

  body {
    font-size: 17px;
    line-height: 23px;
  }
}


@media screen and (max-width: 450px) {
  body {
    font-size: 15px;
    line-height: 20px;
  }

  .keys>div:nth-child(1),
  .keys>div:nth-child(2) {
    display: none;
  }

  .header {
    height: 23px;
    overflow: hidden;
  }

  .mainContent {
    border: 2px solid #000;
    top: 75px;
    bottom: 80px;
    left: 8px;
    right: 8px;
  }

  .keys {
    height: 50px;
  }

  .leftPanelInner,
  .rightPanelHead,
  .explicationText {
    padding: 5px;
  }

}


@media print {
  color: #000;
  background-color: #FFF;
}

.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(0);
}

.modal__content {
  padding: 20px;
  border: 5px solid #0426EA;
  background-color: #CCCCCC;
  width: 400px;
  height: 200px;
  margin: 0 auto;
}

.modal__content__error {
  padding: 20px;
  border: 5px solid #0426EA;
  background-color: #CCCCCC;
  width: 900px;
  height: 500px;
  position: absolute;
  left: 25%;
  z-index: 1002;
}

.modal__items {
  display: flex;
  justify-content: center;
  align-self: center;
}

.modal.active {
  transform: scale(1);
}

.modal__items.active {
  color: white;
  background-color: #0426EA;
}

.not__detected {
  color: #555;
}

.block--sensor {
  background-color: greenyellow;
  position: absolute;
  bottom: 0px;

  width: 100%;
  max-height: 250px;
  transition: height 1s ease;


}

.btn--error {
  display: table-cell;
  justify-content: space-between;
  color: #0426EA;
  border: none;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  background: transparent;
  font-family: 'Press Start 2P', cursive;

  margin: 0;
  padding: 6px;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  background: transparent;
  border: 4px solid #0426EA;

}

.btn--error:hover {
  background-color: #0426EA;
  color: white;
}


.wrapper--btn {
  margin: 100px 0;
  display: flex;
  justify-content: space-around;
}

.text__error {
  margin: 75px;
}

